@use "base/variables" as v;
@use "base/mixins" as m;

.sortGESC-body {
    margin-left: 40rem;

    .general-info {
        display: flex;
        justify-content: space-between;
        margin-right: 5rem;
        margin-bottom: 5rem;

        .text {
            h3 {
                color: #7e9ac0;
                font-family: LeagueSpartan, sans-serif;
                font-size: calc(1.5rem + .5vw);
                font-weight: 700;
            }

            .info-pdf {
                display: flex;

                .info-pdf-text {
                    width: 30rem;

                    &>* {
                        margin: 0;
                        font-size: calc(0.5rem + .5vw);
                    }
                    span {
                        color: v.$darkBlue;
                        font-weight: bold;
                    }
                    .ip {
                        font-weight: bold;
                    }
                }

                a {
                    img {
                        margin-top: 1rem;
                        width: 5rem;
                        margin-left: 10rem;

                        &:hover {
                            transform: scale(1.1);
                            transition: all 0.25s ease-in-out;
                        }
                    }
                }
            }
        }

        .img {
            img {
                width: 10rem;
                border-radius: 5rem;
            }
        }
    }

    .nav-section {
        margin-right: 5rem;

        .nav-tabs {
            width: 100%;
            flex-wrap: nowrap;
            align-items: end;

            .nav-item {
                width: 25%;
                background-color: v.$white;
                color: v.$black;

                button {
                    width: 100%;
                    background-color: v.$white;
                    color: v.$white;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    text-transform: uppercase;

                    img {
                        height: 2rem;
                        margin-left: 2rem;
                    }

                    &:hover {
                        padding-top: 1.25rem;
                        padding-bottom: 1.25rem;
                        transition: all 0.25s ease-in-out;
                    }
                }

                #alta-tab {
                    background-color: v.$pink;
                }

                #checklist-tab {
                    background-color: v.$darkBlue;
                }

                #seguro-tab {
                    background-color: v.$greenMilitary;
                }

                #psico-tab {
                    background-color: v.$darkBlueGreen;
                }

                #cita-tab {
                    background-color: v.$purple;
                }

                .active {
                    padding-top: 1.25rem;
                    padding-bottom: 1.25rem;
                }
            }
        }

        .tab-content {
            .accordion {
                .accordion-item {
                    margin-top: 0.5rem;

                    h2 {
                        button {
                            color: v.$black;
                            border: none;
                            border-radius: 0 !important;
                        }
                    }
                }
            }

            #alta {
                button {
                    background-color: lighten(v.$pink, 25%);
                }
            }

            #checklist {
                button {
                    background-color: lighten(v.$darkBlue, 50%);
                }
            }

            #seguro {
                button {
                    background-color: lighten(v.$greenMilitary, 25%);
                }
            }

            #psico {
                button {
                    background-color: lighten(v.$darkBlueGreen, 15%);;
                }
            }

            #cita {
                button {
                    background-color: lighten(v.$purple, 40%);
                }
            }
        }
    }
}

$width_sortGESC_1: 9vw;
$width_sortGESC_2: 9vw;
$width_sortGESC_3: 9vw;
$width_sortGESC_4: 8vw;
$width_sortGESC_5: 11vw;
$width_sortGESC_6: 6vw;
$width_sortGESC_7: 10vw;
$width_sortGESC_8: 7vw;
$width_sortGESC_9: 3vw;
$width_sortGESC_10: 4vw;

.sortGESC-list-body {
    .table-colgroup {
        col {
            &:nth-child(1) {
                width: $width_sortGESC_1;
            }

            &:nth-child(2) {
                width: $width_sortGESC_2;
            }

            &:nth-child(3) {
                width: $width_sortGESC_3;
            }

            &:nth-child(4) {
                width: $width_sortGESC_4;
            }

            &:nth-child(5) {
                width: $width_sortGESC_5;
            }

            &:nth-child(6) {
                width: $width_sortGESC_6;
            }

            &:nth-child(7) {
                width: $width_sortGESC_7;
            }

            &:nth-child(8) {
                width: $width_sortGESC_8;
            }

            &:nth-child(9) {
                width: $width_sortGESC_9;
            }

            &:nth-child(10) {
                width: $width_sortGESC_10;
            }
        }
    }

    .table-scroll {
        $width_sortGESC__extra: 0.85vw;
        max-width: $width_sortGESC_1 + $width_sortGESC_2 + $width_sortGESC_3 + $width_sortGESC_4 + $width_sortGESC_5 + $width_sortGESC_6 + $width_sortGESC_7 + $width_sortGESC_8 + $width_sortGESC_9 + $width_sortGESC_10 + $width_sortGESC__extra;
    }

    select {
        width: 14rem;
    }
}