@use "base/variables" as v;
@use "base/mixins" as m;

$width_babycloud_ip_1: 15vw;
$width_babycloud_ip_2: 10vw;
$width_babycloud_ip_3: 11vw;
$width_babycloud_ip_4: 11vw;
$width_babycloud_ip_5: 11vw;
$width_babycloud_ip_6: 3vw;
$width_babycloud_ip_7: 3vw;
$width_babycloud_ip_8: 3vw;
$width_babycloud_ip_9: 3vw;
$width_babycloud_ip_10: 3vw;
$width_babycloud_ip_11: 3vw;

.babycloud-ip-admin-body,
.babycloud-ip-body {

    .thead-babycloud_ip {
        background-color: v.$white;

        th {
            text-align: center;
        }
    }

    .thead {
        background-color: v.$blue;
        font-weight: normal;
        color: v.$white;
        border-left: 1px solid v.$blue;
        border-right: 1px solid v.$blue;
    }

    .thead-2 {
        font-size: calc(0.6rem + 0.25vw);
        border-left: 1px solid v.$pink;
        border-right: 1px solid v.$pink;
    }

    td {
        font-size: calc(0.6rem + 0.25vw);
        border: 1px solid darken($color: v.$gray, $amount: 10);
    }

    .td-icon {
        button {
            padding: 0;
        }
    }

    .td-info-title {
        background-color: v.$white;
        color: v.$pink;
        border-radius: 50rem;
        text-align: center;
    }

    .td-double-border {
        background-color: v.$pink;
        height: 1rem;
    }

    .td-select {
        width: 7vw;
    }

    .row-first {
        background-color: darken(v.$gray, 5%);
    }

    td {
        padding-top: 0 !important;
        padding-bottom: 0 !important;
    }

    .thead {
        button {
            background-color: v.$pink;
            font-size: calc(0.6rem + 0.25vw);
            padding: 0.5rem 1rem;
            font-weight: bold;
            margin: 0;
            .fa-plus,
            .fa-minus {
                border: 1px solid v.$white;
                padding: 0.2rem;
                border-radius: 10rem;
                color: v.$pink;
                background-color: v.$white;
                margin-left: 0.5rem;
            }
            &:hover {
                background-color: darken($color: v.$pink, $amount: 10);
            }
            &:disabled {
                background-color: lighten($color: v.$pink, $amount: 20);
                &:hover {
                    background-color: lighten($color: v.$pink, $amount: 20);
                }
            }
        }
    }

    .table-colgroup {
        col {
            &:nth-child(1) {
                width: $width_babycloud_ip_1;
            }

            &:nth-child(2) {
                width: $width_babycloud_ip_2;
            }

            &:nth-child(3) {
                width: $width_babycloud_ip_3;
            }

            &:nth-child(4) {
                width: $width_babycloud_ip_4;
            }

            &:nth-child(5) {
                width: $width_babycloud_ip_5;
            }

            &:nth-child(6) {
                width: $width_babycloud_ip_6;
            }

            &:nth-child(7) {
                width: $width_babycloud_ip_7;
            }

            &:nth-child(8) {
                width: $width_babycloud_ip_8;
            }

            &:nth-child(9) {
                width: $width_babycloud_ip_9;
            }

            &:nth-child(10) {
                width: $width_babycloud_ip_10;
            }

            &:nth-child(11) {
                width: $width_babycloud_ip_11;
            }
        }
    }

    .table-scroll {
        $width_babycloud_ip__extra: 0.75vw;
        max-width: $width_babycloud_ip_1 + $width_babycloud_ip_2 + $width_babycloud_ip_3 + $width_babycloud_ip_4 + $width_babycloud_ip_5 + $width_babycloud_ip_6 + $width_babycloud_ip_7 + $width_babycloud_ip_8 + $width_babycloud_ip_9 + $width_babycloud_ip_10 + $width_babycloud_ip_11 + $width_babycloud_ip__extra;
    }

    .td-info {
        color: v.$pink;
        width: 0.5rem;
        height: 0.5rem;
    }

    .bi-file-earmark-pdf-fill {
        color: v.$red;
    }

    .bi-hourglass-split {
        color: v.$darkGray;
    }

    tbody:last-of-type {
        tr:last-of-type {
            background-color: v.$pink !important;
            td {
                color: v.$white;
                font-weight: bold;
            }
            .td-info-two-cols {
                color: v.$black;
            }

            .uploading-true,
            .fa-eye,
            .fa-toggle-on {
                color: darken($color: v.$pink, $amount: 40);
            }

            .uploading-false,
            .fa-eye-slash,
            .fa-toggle-off {
                color: lighten($color: v.$pink, $amount: 20);
            }
        }
    }

}

.babycloud-ip-admin-body {
    .row-first {
        background-color: darken(v.$gray, 5%);
    }

    td {
        padding-top: 0 !important;
        padding-bottom: 0 !important;
    }

    input[type="date"] {
        color: v.$white;
        background-color: v.$blue;
        border: 1px solid #7e9ac0;
        border-radius: .5rem;
        padding: .5rem 1rem;

        &::-webkit-calendar-picker-indicator {
            filter: invert(1);
        }

        &:focus-visible {
            outline: none;
        }
    }

    .table-colgroup {
        col {
            &:nth-child(1) {
                width: $width_babycloud_ip_1;
            }

            &:nth-child(2) {
                width: $width_babycloud_ip_2;
            }

            &:nth-child(3) {
                width: $width_babycloud_ip_3;
            }

            &:nth-child(4) {
                width: $width_babycloud_ip_4;
            }

            &:nth-child(5) {
                width: $width_babycloud_ip_5;
            }

            &:nth-child(6) {
                width: $width_babycloud_ip_6;
            }

            &:nth-child(7) {
                width: $width_babycloud_ip_7;
            }

            &:nth-child(8) {
                width: $width_babycloud_ip_8;
            }

            &:nth-child(9) {
                width: $width_babycloud_ip_9;
            }

            &:nth-child(10) {
                width: $width_babycloud_ip_10;
            }
        }
    }

    .table-scroll {
        $width_babycloud_ip__extra: 0.85vw;
        max-width: $width_babycloud_ip_1 + $width_babycloud_ip_2 + $width_babycloud_ip_3 + $width_babycloud_ip_4 + $width_babycloud_ip_5 + $width_babycloud_ip_6 + $width_babycloud_ip_7 + $width_babycloud_ip_8 + $width_babycloud_ip_9 + $width_babycloud_ip_10 + $width_babycloud_ip__extra;
    }

    table {
        .td-info-two-cols {
            background-color: lighten(v.$blue, 25);
            padding: 0.25rem 1rem;
            margin: 1rem;

            p {
                margin-bottom: 0 !important;
            }
        }
        .td-inline-block {
            p:last-child {
                background-color: lighten(v.$blue, 25);
            }
        }
    }
}

